export const findCitiesCoord = (city: string):Array<number>|undefined => {
    let found = cities.find(data => data[0]===city)
    if (found===undefined) return undefined
    return [found[1] as number, found[2] as number]
}


function getDistanceFromLatLonInKm( point1:Array<number>, point2:Array<number> ) {
	// если точки совпадают, то выходим из функции, возвращаем 0
	if (( point1[0] === point2[0]) && (point1[1] === point2[1]))
		return 0

    const radlat1 = Math.PI * point1[0] / 180
    const radlat2 = Math.PI * point2[0] / 180
    const theta = point1[1] - point2[1]
    const radtheta = Math.PI * theta/180
    let dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta)
    if (dist > 1) dist = 1
    dist = Math.acos(dist)
    dist = dist * 180/Math.PI
    dist = dist * 60 * 1.1515

    // чтобы получить в км
    dist = dist * 1.609344
    return dist
} 

export const findNearCity = (coord: Array<number>):string => {
    let nearCityData = {"distance": 100000, "city": "Mars"}

    cities.forEach(current => {
        const distance = getDistanceFromLatLonInKm([current[1] as number,current[2] as number], coord)

        if (distance < nearCityData.distance) 
            nearCityData = {"distance": distance, "city": current[0] as string}
    })

    return nearCityData.city
}